body {
  margin: 0;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  font-family: Arial, sans-serif;
}

.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

textarea {
  width: 300px;
  height: 120px;
  padding: 10px;
  border-radius: 10px;
  font-size: 16px;
  outline: none;
  border: none;
}

p {
  color: white;
  margin-top: 10px;
  font-size: 16px;
}

.red {
  color: red;
  font-weight: bold;
}
